@import '../App.scss';

.filters-container {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  justify-content: center;
  align-items: start;
  gap: 12px 12px;
  padding: 24px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: $main-color;
  border: none;
  border-radius: 12px;
}

.filter {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 6px 0;
  position: relative;
  height: auto;
  width: auto;
}

.filter-name {
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  text-align: left;
}

.filter-select {
  box-sizing: border-box;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  border: none;
  border-radius: 6px;
}

.filter-pricepool-value-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
}

.filter-pricepool-value-minimum {
  color: $description-text-color;
  font-size: $tiny-font-size;
  font-weight: $thin-font-weight;
  font-family: $secondary-font-family;
  text-align: left;
}

.filter-pricepool-value-maximum {
  color: $description-text-color;
  font-size: $tiny-font-size;
  font-weight: $thin-font-weight;
  font-family: $secondary-font-family;
  text-align: right;
}

.filter-pricepool-container {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 12px;
  width: 100%;
}

.filter-pricepool-field-container {
  overflow: hidden;
  position: relative;
  height: 6px;
  width: 100%;
  transform: translateY(-50%);
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.filter-pricepool-field-active {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: $additional-color;
  border: none;
  border-radius: 6px;
}

.filter-pricepool-control-minimum {
  box-sizing: border-box;
  padding: 6px;
  position: absolute;
  top: 50%;
  left: 0;
  height: 12px;
  width: 12px;
  transform: translateY(-50%);
  background-color: $inverted-main-color;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.filter-pricepool-control-maximum {
  box-sizing: border-box;
  padding: 6px;
  position: absolute;
  top: 50%;
  right: 0;
  height: 12px;
  width: 12px;
  transform: translateY(-50%);
  background-color: $inverted-main-color;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.applied-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  padding: 12px;
  position: sticky;
  top: 12px;
  height: auto;
  width: 100%;
  background-color: $main-color;
  border: none;
  border-radius: 12px;
  z-index: 2;
}

.products-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 12px 12px;
  justify-content: center;
  align-items: start;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.product {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 12px 0;
  padding: 12px;
  position: relative;
  height: auto;
  width: auto;
  text-align: left;
  background-color: $main-color;
  border: none;
  border-radius: 12px;
}

.product-type {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 12px 6px;
  margin-bottom: 6px;
  position: relative;
  height: auto;
  width: 100%;
}

.product-type-icon {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $main-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
}

.product-type-name {
  color: $main-text-color;
  font-size: $subtitle-font-size;
  font-weight: $bold-font-weight;
  font-family: $secondary-font-family;
  text-transform: uppercase;
  text-align: left;
}

.product-sort-description-title {
  display: flex;
  flex-direction: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
  color: $title-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  cursor: pointer;
}

.product-sort-description-title i {
  transform: scaleY(1);
  transition: 0.1s;
}

.product-sort-description-title i.shown {
  transform: scaleY(-1);
  transition: 0.1s;
}

.product-sort-description-container {
  margin-bottom: -12px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 12px 0;
  position: relative;
  height: 0;
  width: 100%;
  transform: scaleY(0);
  transform-origin: top;
  transition: 0s;
  opacity: 0;
  user-select: text;
}

.product-sort-description-container.shown {
  margin-bottom: 0;
  height: auto;
  transform: scaleY(100%);
  transition: 0.1s;
  opacity: 1;
}

.product-sort-description-text {
  position: relative;
  height: auto;
  width: 100%;
  color: $description-text-color;
  font-size: $small-font-size;
  font-weight: $main-font-weight;
  font-family: $main-font-family;
}

.product-sort-description-actions-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.product-sort-description-action {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
}

.product-characteristics-title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
  color: $title-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  cursor: pointer;
}

.product-characteristics-title i {
  transform: scaleY(1);
  transition: 0.1s;
}

.product-characteristics-title i.shown {
  transform: scaleY(-1);
  transition: 0.1s;
}

.product-characteristics-container {
  margin-bottom: -12px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: 0;
  width: 100%;
  transform: scaleY(0);
  transform-origin: top;
  transition: 0s;
  opacity: 0;
}

.product-characteristics-container.shown {
  margin-bottom: 0;
  height: auto;
  transform: scaleY(100%);
  transition: 0.1s;
  opacity: 1;
}

.product-characteristic {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  color: $description-text-color;
  font-size: $description-font-size;
  font-weight: $main-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
}

.product-characteristic.small {
  width: calc(100% / 2 - 3px);
}

.product-characteristic-name {
  color: $description-text-color;
  font-size: $small-font-size;
  font-weight: $main-font-weight;
  font-family: $secondary-font-family;
}

.product-characteristic-value {
  color: $subtitle-text-color;
  font-size: $description-font-size;
  font-weight: $main-font-weight;
  font-family: $secondary-font-family;
}

.product-cost-container {
  margin-top: 12px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
}

.product-cost-weight-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  position: relative;
  height: auto;
  width: auto;
}

.product-cost-weight-remove {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $main-text-color;
  font-size: $small-font-size;
  font-weight: $main-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
  line-height: 1.25;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.product-cost-weight-value {
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 52px;
  text-align: center;
  color: $description-text-color;
  font-size: $small-font-size;
  font-weight: $main-font-weight;
  font-family: $additional-font-family;
  border: none;
  border-radius: 6px;
}

.product-cost-weight-add {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $main-text-color;
  font-size: $small-font-size;
  font-weight: $main-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
  line-height: 1.25;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.product-cost {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  padding: 6px 12px;
  position: relative;
  height: 26px;
  width: auto;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $additional-font-family;
  background-color: $main-element-color;
  border: none;
  border-radius: 6px;
}

.product-view {
  position: relative;
  height: auto;
  width: 100%;
}