@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$background-color: rgb(0, 0, 0);
$main-color: rgb(20, 20, 20);
$inverted-main-color: rgb(230, 230, 230);
$main-element-color: rgba(250, 250, 250, 0.05);
$additional-color: rgb(100, 0, 250);
$main-text-color: rgb(250, 250, 250);
$title-text-color: rgb(220, 220, 220);
$subtitle-text-color: rgb(180, 180, 180);
$description-text-color: rgb(140, 140, 140);
$link-text-color: rgb(0, 150, 255);
$main-font-family: 'Raleway', sans-serif;
$secondary-font-family: 'Kanit', sans-serif;
$additional-font-family: 'Franklin Gothic Medium', sans-serif;
$title-font-size: 20px;
$wide-font-size: 18px;
$subtitle-font-size: 16px;
$description-font-size: 14px;
$small-font-size: 12px;
$tiny-font-size: 10px;
$wide-font-weight: 900;
$bold-font-weight: 700;
$main-font-weight: 500;
$thin-font-weight: 300;
$tiny-font-weight: 100;

body {
  margin: 0;
  padding: 0;
  background-color: $background-color;
  user-select: none;
}

a {
  text-decoration: none;
}

input {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  outline: none;
  border: none;
  border-radius: 12px;
}

select {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $subtitle-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
  outline: none;
  border: none;
  border-radius: 12px;
}

option {
  color: black;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
}

.button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  background-color: $additional-color;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.1s;

  &:hover {
    transform: scale(102%);
    transition: 0.1s;
  }

  &:active {
    transform: scale(100%);
    filter: brightness(40%);
    transition: 0.05s;
  }
}

#root {
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.section {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px 0;
  padding: 24px;
  position: relative;
  height: auto;
  width: 100%;
}